import { combineReducers, applyMiddleware, createStore, compose } from "redux";
import thunk from 'redux-thunk';
import reduxImmutableStateInvariant from 'redux-immutable-state-invariant';
import controllers from '../reducers/controllers';
import controllerIndex from '../reducers/controllerIndex';

export const rootReducer = combineReducers({
	controllers: controllers,
	controllerIndex: controllerIndex
});

export default () => {

	const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

	const store = createStore(rootReducer,
		// TODO: remove reduxImmutableStateInvariant() from middleware for production build
		// should we remove redux_dev_tools too for production build?
		// composeEnhancers(applyMiddleware(thunk, reduxImmutableStateInvariant()))
		// COMMENT THE ABOVE, YOU CAN USE THE BELOW FOR PRODUCTION BUILDS
		compose(applyMiddleware(thunk))
	);
	return store;
};



