import initialState from './initialState';

export default (state = initialState.controllers, action) => {
	switch (action.type) {
		case 'DELETE_CONTROLLERS':
			return []
		case 'ADD_CONTROLLER':
			return [...state, action.payload];
		case 'UPDATE_CONTROLLER':
			let newState = [ ...state ];
			newState = newState.map(controller => {
				if (controller.ControllerName === action.payload.ControllerName) {
					
					const newControllerState = { ...controller }
					newControllerState.Devices = newControllerState.Devices.map(device => {
						if(device.DeviceName === action.payload.DeviceName){
							return { ...device, DeviceState: action.payload.DeviceState }
						}
						return device
					})
					return newControllerState
				}
				return controller;
			});
			return newState;
		default:
			return state;
	}
};



